import React from 'react';
import axios from "axios"
import { connect } from "react-redux";
import {logout} from '../../redux/actions/userAction'
function Logout({ history ,logout}) {
    const removeheader = () => {
        logout()
        axios.defaults.headers.common["Authorization"] && delete axios.defaults.headers.common["Authorization"];
        axios.defaults.headers.common["Domain"] && delete axios.defaults.headers.common["Domain"];
        history.push('/login');
    }
    return (
        <div className="container mt-5 text-center">
            <div className="">

                <h2 className="text-center"
                    className="btn btn-info"
                    onClick={removeheader}
                >
                    Logout
                </h2>

            </div>
        </div>
    );
}

export default connect(null,{logout})(Logout);
